const getDateRange = (date: Date, range: string): [Date, Date] => {
  let startDate, endDate;
  switch (range) {
    case 'This Week': {
      const dayOfWeek = date.getDay();
      startDate = new Date(date.getTime() - dayOfWeek * 24 * 60 * 60 * 1000);
      endDate = new Date(startDate.getTime() + 6 * 24 * 60 * 60 * 1000);
      break;
    }
    case 'Last Week': {
      const dayOfWeek = date.getDay();
      startDate = new Date(
        date.getTime() - (dayOfWeek + 7) * 24 * 60 * 60 * 1000
      );
      endDate = new Date(startDate.getTime() + 6 * 24 * 60 * 60 * 1000);
      break;
    }
    case 'This Month': {
      startDate = new Date(date.getFullYear(), date.getMonth(), 1);
      endDate = new Date(date.getFullYear(), date.getMonth() + 1, 0);
      break;
    }
    case 'Last Month': {
      startDate = new Date(date.getFullYear(), date.getMonth() - 1, 1);
      endDate = new Date(date.getFullYear(), date.getMonth(), 0);
      break;
    }
    case 'This Year': {
      startDate = new Date(date.getFullYear(), 0, 1);
      endDate = new Date(date.getFullYear(), 11, 31);
      break;
    }
    case 'Last Year': {
      startDate = new Date(date.getFullYear() - 1, 0, 1);
      endDate = new Date(date.getFullYear() - 1, 11, 31);
      break;
    }
  }
  return [startDate, endDate];
};

const today = new Date();
const [startDate, endDate] = getDateRange(today, 'This Week');

type CollectionAmountItem = {
  [key: string]: any;
  [key: number]: any;
};

const getValuesForProperty = <T extends CollectionAmountItem>(
  arr: T[],
  propName: keyof T
): any[] => {
  return arr.map((item) => item[propName] ?? 0);
};

const move7002ToFirst = (array: any[]) => {
  array.sort((a, b) => {
    if (a.id === 7002) {
      return -1; // move a to the beginning of the array
    }
    if (b.id === 7002) {
      return 1; // move b to the beginning of the array
    }
    return a.id - b.id; // sort by id if neither a nor b has id of 7002
  });
  return array; // return the sorted array
};

const addPropertyToObjects = (
  arr: any[],
  propName: string,
  propValue: any
): any[] => {
  return arr.map((item: any) => {
    return {
      ...item,
      [propName]: propValue,
    };
  });
};

