  interVal() {
    this.interValId = setInterval(() => { this.onInitFunctions() }, 180000);
  }
  onInitFunctions() {
    this.GetLookups(7);
    this.GetDashboardSessionsBlock();
    this.GetAllSessions();
    this.GetDashboardRegisters();
    this.GetRegisters('2001', 0, 1000);
    this.GetDashboardSessions('closed');
    this.GetDashboardSessions('unclosed');
    this.headerService.setTitle('Dashboard');
    this.GetRegistersStatistics(
      startDate.toLocaleDateString(),
      endDate.toLocaleDateString()
    );
    this.GetDashboardSettledAmounts(
      startDate.toLocaleDateString(),
      endDate.toLocaleDateString()
    );
    this.GetSettledSessionsCollections(
      startDate.toLocaleDateString(),
      endDate.toLocaleDateString()
    );
    this.GetDashboardAllocationsAmount(
      startDate.toLocaleDateString(),
      endDate.toLocaleDateString()
    );
    this.GetCollectionsByOrderType(
      this.selectedTabId,
      startDate.toLocaleDateString(),
      endDate.toLocaleDateString()
    );
  }
  ngOnDestroy() {
    clearInterval(this.interValId);
  }
