import { Component, EventEmitter, Input, Output } from '@angular/core';
import { DashboardService } from '../dashboard.service';

@Component({
  selector: 'online-registers',
  templateUrl: './online-registers.component.html',
  styleUrls: ['../dashboard.component.scss', './online-registers.component.scss']
})
export class OnlineRegistersComponent {
  totalRecords = 0;
  pageNo = 1;
  searchQuery = '';
  @Input() regsiterList;
  @Output() registerEvent = new EventEmitter();
  eventData;
  constructor(
    private _dashboardService: DashboardService,
  ) { }
  ngOnChanges() {
    this.totalRecords = this.regsiterList.info.totalRecordsCount;
  }
  ngOnInit() {
  }
  onSearchChange(event) {
    this.pageNo = 1;
    let value = event.target.value;
    this.registerEvent.emit({ ...this.eventData, search: value, pageNo: this.pageNo });
  }
  clearSearch() {
    this.pageNo = 1;
    this.searchQuery = '';
    this.registerEvent.emit({ ...this.eventData, search: '', pageNo: this.pageNo });
  }
  pageChange(pageNo) {
    this.registerEvent.emit({ ...this.eventData, pageNo });
  }
}
